// Импорт стилей и данных
import './pages/index.css';
import initialCards from './cards.js';
import avatar from './images/avatar.jpeg';
import { createCard, deleteCard, likeCard } from './components/card.js';
import { openPopup, closePopup, addOverlayCloseHandler } from './components/modal.js';

// Получение шаблона карточки и списка для карточек
const cardTemplate = document.querySelector('#card-template').content;
const cardList = document.querySelector('.places__list');

// Константы DOM
const editProfilePopup = document.querySelector('.popup_type_edit');
const newCardPopup = document.querySelector('.popup_type_new-card');
const imagePopup = document.querySelector('.popup_type_image');
const imagePopupImage = imagePopup.querySelector('.popup__image');
const imagePopupCaption = imagePopup.querySelector('.popup__caption');
const editProfileButton = document.querySelector('.profile__edit-button');
const addCardButton = document.querySelector('.profile__add-button');
const closeButtons = document.querySelectorAll('.popup__close');
const profileTitle = document.querySelector('.profile__title');
const profileDescription = document.querySelector('.profile__description');

// Установка фото профиля
document.querySelector('.profile__image').style.backgroundImage = `url(${avatar})`;

// Функция открытия попапа с изображением
export function openImagePopup(imageSrc, imageAlt) {
  imagePopupImage.src = imageSrc;
  imagePopupImage.alt = imageAlt;
  imagePopupCaption.textContent = imageAlt;
  openPopup(imagePopup);
}

// Функция вывода карточек
function showCards(cards) {
  cards.forEach((cardData) => {
    const cardElement = createCard(cardTemplate, cardData, deleteCard, likeCard, openImagePopup);
    cardList.append(cardElement);
  });
}

// Инициализация карточек при загрузке страницы
showCards(initialCards);

// Получение элементов формы редактирования профиля
const editProfileForm = document.querySelector('.popup__form[name="edit-profile"]');
const nameInput = editProfileForm.querySelector('.popup__input_type_name');
const jobInput = editProfileForm.querySelector('.popup__input_type_description');

// Регулярные выражения для валидации данных
const nameRegex = /^[а-яА-Яa-zA-Z\- ]+$/;
const jobRegex = /^[а-яА-Яa-zA-Z\- ]+$/;

// Функция проверки валидности данных
function isValidInput(inputValue, minLength, maxLength, pattern) {
  return pattern.test(inputValue) && inputValue.length >= minLength && inputValue.length <= maxLength;
}

function showInputError(inputElement, errorMessage) {
  const errorElement = inputElement.nextElementSibling;
  errorElement.textContent = errorMessage;
  errorElement.classList.add('popup__input-error_visible');
}

function hideInputError(inputElement) {
  const errorElement = inputElement.nextElementSibling;
  errorElement.textContent = '';
  errorElement.classList.remove('popup__input-error_visible');
}

// Функция очистки ошибок валидации
function clearValidationErrors() {
  const errorElements = document.querySelectorAll('.popup__input-error');
  errorElements.forEach(error => hideInputError(error.previousElementSibling));
}

// Функция обновления состояния кнопки "Сохранить"
function toggleSaveButtonState(isValid) {
  const saveButton = editProfileForm.querySelector('.button.popup__button');
  if (isValid) {
    saveButton.removeAttribute('disabled');
    saveButton.classList.remove('popup__button_disabled');
  } else {
    saveButton.setAttribute('disabled', true);
    saveButton.classList.add('popup__button_disabled');
  }
}

// Обработчик события submit для формы редактирования профиля
function handleEditProfileFormSubmit(evt) {
  evt.preventDefault();

  const newName = nameInput.value.trim();
  const newJob = jobInput.value.trim();
  let isValid = true;

  if (!isValidInput(newName, 2, 40, nameRegex)) {
    showInputError(nameInput, 'Введите от 2 до 40 символов, используя только буквы, дефисы и пробелы');
    isValid = false;
  } else {
    hideInputError(nameInput);
  }

  if (!isValidInput(newJob, 2, 200, jobRegex)) {
    showInputError(jobInput, 'Введите от 2 до 200 символов, используя только буквы, дефисы и пробелы');
    isValid = false;
  } else {
    hideInputError(jobInput);
  }

  if (isValid) {
    profileTitle.textContent = newName;
    profileDescription.textContent = newJob;
    closePopup(editProfilePopup);
  }

  toggleSaveButtonState(isValid);
}

// Слушатель события submit для формы редактирования профиля
editProfileForm.addEventListener('submit', handleEditProfileFormSubmit);

// Очистка ошибок при открытии формы редактирования профиля
editProfileButton.addEventListener('click', () => {
  openPopup(editProfilePopup);
  nameInput.value = profileTitle.textContent;
  jobInput.value = profileDescription.textContent;
  clearValidationErrors();
  toggleSaveButtonState(true); // По умолчанию кнопка активна
});

// Получение элементов формы для новой карточки
const newCardForm = document.querySelector('.popup__form[name="new-place"]');
const cardNameInput = newCardForm.querySelector('.popup__input_type_card-name');
const cardLinkInput = newCardForm.querySelector('.popup__input_type_url');

// Обработчик события submit для формы новой карточки
function handleNewCardFormSubmit(evt) {
  evt.preventDefault();
  const cardName = cardNameInput.value;
  const cardLink = cardLinkInput.value;

  const newCard = createCard(cardTemplate, { name: cardName, link: cardLink }, deleteCard, likeCard, openImagePopup);
  cardList.prepend(newCard);

  newCardForm.reset();
  closePopup(newCardPopup);
}

newCardForm.addEventListener('submit', handleNewCardFormSubmit);

// Обработчики событий для открытия попапов
addCardButton.addEventListener('click', () => openPopup(newCardPopup));

closeButtons.forEach((button) => {
  const popup = button.closest('.popup');
  button.addEventListener('click', () => closePopup(popup));
  addOverlayCloseHandler(popup); // Добавляем обработчик клика на оверлей для каждого попапа
});

// Обработчики для ввода
nameInput.addEventListener('input', () => {
  if (isValidInput(nameInput.value.trim(), 2, 40, nameRegex)) {
    hideInputError(nameInput);
  }
  toggleSaveButtonState(isValidInput(nameInput.value.trim(), 2, 40, nameRegex) && isValidInput(jobInput.value.trim(), 2, 200, jobRegex));
});

jobInput.addEventListener('input', () => {
  if (isValidInput(jobInput.value.trim(), 2, 200, jobRegex)) {
    hideInputError(jobInput);
  }
  toggleSaveButtonState(isValidInput(nameInput.value.trim(), 2, 40, nameRegex) && isValidInput(jobInput.value.trim(), 2, 200, jobRegex));
});
